import { Component, h, Prop } from '@stencil/core';


@Component({
      tag: 'ks-order-country-select'
})
export class OrderCountrySelect {

    @Prop() name: string;
    @Prop() label: string;

    @Prop() connectTo: string;
    @Prop() lightUp: boolean = false;

    Change(e: Event) {
        if(this.connectTo) {
            const connect = document.querySelector(`ks-input-select-dynamic[name="${this.connectTo}"]`);
            const select = e.target as HTMLSelectElement;

            connect.setAttribute("api-data", select.value);
            connect.setAttribute("light-up", "false");
        }
    }

	render() {            
        return(
            <ks-input-select name={this.name} label={this.label} onChange={(e)=>this.Change(e)} light-up={this.lightUp}>
                <slot />
            </ks-input-select>
        );
    }
}
